
import { defineComponent } from 'vue';
import Goods from '../Goods/Goods.vue';
import GoodsItem from '../GoodsItem/GoodsItem.vue';

export default defineComponent({
  components: { Goods, GoodsItem },
  props: {
    slides: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    },
    time: {
      type: Number,
      default: 10000
    },
    controlsEnable: {
      type: Boolean,
      default: false
    },
    durationTime: {
      type: Number,
      default: 2000
    }
  },
  computed: {
    splitedArrays: function () {
      return this.splitItemsToArrays(this.slides);
    }
  },
  methods: {
    goodsItemClick(id: number) {
      this.$emit('goodsItemClicked', id);
    },
    splitItemsToArrays(slides: Array<any>) {
      var splitedArrays = [];

      for (var i = 0; i < slides.length; i += 4) {
        let tempArray;
        tempArray = slides.slice(i, i + 4);
        splitedArrays.push(tempArray);
      }
      return splitedArrays;
    }
  }
});
